import { Component, Vue } from "vue-property-decorator";
import firebaseAuthService from "@/services/FirebaseAuthService";
import MobileVerify from "@/components/otp/MobileVerify.vue";
import EmailVerify from "@/components/EmailVerify/EmailVerify.vue";

import { FirestoreService } from "@/services/FirestoreService";
import FireUser from "@/dto/FireUser";
import { User } from "firebase/auth";
import LoginImageInfo from "@/dto/LoginImageInfo";

@Component({
  components: {
    MobileVerify,
    EmailVerify
  }
})
export default class Login extends Vue {
  loginError = "";
  showError = false;
  email = "";
  password = "";
  loading = false;
  loggedIn = false;
  mobileValid = true;
  emailValid = false;
  loginImageInfo: LoginImageInfo = new LoginImageInfo();
  async mounted() { await this.getLoginImage() }
  async emailLogin() {
    try {
      this.loading = true;
      if (this.validateFields()) {
        const signInResponse = await firebaseAuthService.signInWithEmailPassword(
          this.email,
          this.password
        );
        if (signInResponse.user?.emailVerified) {
          this.loggedIn = true;
          this.validateEmailMobile(signInResponse.user);
        } else {
          this.loginError = "Please verify your email";
          this.showError = true;
        }
      }
    } catch (error) {
      this.loginError = "Invalid user credentials";
      this.showError = true;
    } finally {
      this.loading = false;
    }
  }
  async validateEmailMobile(user: User | null | undefined) {
    if (user) {
      const snapshot: FireUser = await FirestoreService.getSingleDocument(
        "users",
        user.uid
      );
      this.mobileValid = snapshot.mobileVerified;
    }
  }

  async googleLogin() {
    try {

      const signInResponse = await firebaseAuthService.signIn();
      if (signInResponse && signInResponse.user?.emailVerified) {
        this.loggedIn = true;
        await this.validateEmailMobile(signInResponse?.user);
        this.$router.push("/");
      } else {
        this.loginError = "Something went wrong. Please try again";
        this.showError = true;
      }

    } catch (error) {
      this.loginError = "Invalid user credentials";
      this.showError = true;
    } finally {
      this.loading = false;
    }
  }
  validateFields() {
    const validationResult =
      this.validateEmail(this.email) === true
        ? this.validatePassword(this.password) === true
          ? true
          : false
        : false;
    if (validationResult) {
      this.loginError = "";
      this.showError = false;
    }
    return validationResult;
  }
  validateEmail(email: string) {
    const validateEmail = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
      email.toLowerCase()
    );
    if (!validateEmail) {
      this.loginError = "Enter Valid Email Address";
      this.showError = true;
    }
    return validateEmail;
  }
  validatePassword(password: string) {
    const notEmptyPassword = password.length > 0 ? true : false;
    const validatePassword = password.length > 8 ? true : false;
    if (!notEmptyPassword) {
      this.loginError = "Password can not be empty";
      this.showError = true;
    }

    return notEmptyPassword;
  }
  async getLoginImage() {
    try {
      const snapshot = await FirestoreService.getSingleDocument<LoginImageInfo>(
        "HomeScreen",
        "loginImage",
        LoginImageInfo.converter
      );
      if (snapshot !== null) {
        this.loginImageInfo = snapshot as LoginImageInfo;
      }
    } catch (error) {
      Vue.toasted.show("Error fetching GiftCards Details", {
        icon: "face",
        type: "error"
      });
      console.log(error);
    }
  }
}
